.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    background-color: black;
}

.nav-menu {
    display: flex;
}

.nav-menu li {
    padding: 0 1rem;
}

.nav-menu li a {
    font-size: 1.2rem;
    font-weight: 500;
}

.dropdown {
    display: none;
}

@media screen and (max-width: 1040px) {
    .nav-menu {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        position: absolute;
        top: 0;
        left: -100%;
        z-index: 1;
        transition: 0.5s;
        background-color: black;
        opacity: 97%;
    }

    .nav-menu.active {
        left: 0;
    }

    .nav-menu li {
        padding: 1rem;
    }

    .nav-menu li a {
        font-size: 2rem;
    }

    .dropdown {
        display: initial;
        z-index: 2;
    }
}